.fancy-search-fullscreen-overlay {
    /* Height & width depends on how you want to reveal the overlay (see JS below) */
    height: 100%;
    width: 100%;
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    background-color: rgb(0,0,0); /* Black fallback color */
    background-color: rgba(0,0,0, 0.25); /* Black w/opacity */
    overflow-x: hidden; /* Disable horizontal scroll */
    transition: 0.5s; /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */
}

/* Position the content inside the overlay */
.fancy-search-content {
    position: relative;
    top: 8rem; /* 25% from the top */
    width: 32rem; /* 100% width */
    margin: auto;
    left: 0;
    right: 0;
}

.fancy-search-input {
    text-align: center;
    width: 100%;
    font-size: 24px;
}

/* Position the close button (top right corner) */
.fancy-search-fullscreen-overlay .closebtn {
    position: absolute;
    top: 20px;
    right: 45px;
    font-size: 60px;
}

/* When the height of the screen is less than 450 pixels, change the font-size of the links and position the close button again, so they don't overlap */
@media screen and (max-height: 450px) {
    .fancy-search-fullscreen-overlay button {font-size: 20px}
    .fancy-search-fullscreen-overlay .closebtn {
        font-size: 40px;
        top: 15px;
        right: 35px;
    }
}

.fancy-search-results-container {
    overflow-y: scroll;
    width: 100%;
    max-height: 32rem;
    color: white;
    text-align: left;
}

.fancy-search-result-item {
    /* Add shadows to create the "card" effect */
    margin: 0.5rem 0;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    color: black;
    background-color: white;
    border-radius: 0.25rem;
    padding: 0.25rem 0.5rem;
}




.lds-dual-ring {
    display: block;
    position: relative;
    left: 0;
    right: 0;
    margin: auto;
    padding-top: 2rem;
    width: 80px;
    height: 80px;
}
.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #E040FB;
    border-color: #E040FB transparent #E040FB transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
